* {
  font-family: 'Poppins', sans-serif;
}

/* ===========================header */
.bg-header{
  background-color: turquoise;
}
.NavbarItems{
  background-color: turquoise;
  height: 80px;
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
}


.nav-logo{
  color: white;
  justify-self: start;
  cursor: pointer;
}

.nav-menu{
  display: grid;
  grid-template-columns: repeat(5,auto);
  grid-gap: 15px;
  list-style: none;
  text-align: center;
  justify-content: end;
  padding-top: 10px;
  width: 60vw;
  margin-right: 2rem;
  
}

.nav-links{
  color: white;
  text-decoration:none;
  padding-top: 10px;
}

.nav-links:hover{
  /* background-color: blanchedalmond; */
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.nav-links-mobile{
  /* display: none; */
  color: white;
  text-decoration:none;
}

.menu-icon{
  display: none;
}

h1 a{
  text-decoration: none;
  color: white;
}

h1 a:hover {
  color: rgb(253, 255, 143);
}

/* mobile =================*/
@media screen and (max-width:450px){ 
  .NavbarItems{
    position: relative;
    /* overflow: hidden; */
  }

  .bagian-surah{
    margin: 20px auto;
  }
  .nav-menu{
    background-color: rgb(101, 243, 229);
    display: flex;
    flex-direction: column;
   
    width: 100%;
    height: 300px;
    position: absolute;
    top: 80px;
    left: -150%;
    opacity: 0.1px;
    transition: all 0.5s ease-in-out;
    margin-left: 14px;
    
    
  }
  
 .nav-menu.active{
    /* background: olivedrab; */
    left: -15px;
    width: 100%;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

 .nav-links{
    text-align: center;
    padding: 0.1rem;
    width: 30%;
    display: flex;
    justify-content: space-around;
    justify-content: center;
    align-items: center;
   
    /* display: table; */
  }

 .nav-links:hover{
    /* background-color: rgb(223, 223, 223); */
    /* border-radius: 8px; */
    opacity: 0.8;
    color: black;
    cursor: pointer;
  }

   .nav-logo{
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 40%);
            transform: translate(25%, 40%);
  }

    .menu-icon{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-90%,40%);
            transform: translate(-90%,40%);
    font-size: 1.8rem;
    cursor: pointer;
    color: white;
  }
}
.btn{
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover{
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: rgb(219, 218, 218);
    color: black;
    transition: 250ms;
}

.btn--primary {
    background-color: blue;
}

.btn--outline{
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border: 4px;
    border: 1px solid blue;
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: white;
}

.btn--large{
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 20px;
    color: white;
}
